import 'react-native';
import React from 'react';
import Form, {toMeDate} from '../src/Form';

import renderer from 'react-test-renderer';

describe('Test Form', () => {
  it('Test func transform date', () => {
    const test = new Date()
    expect(toMeDate(test).getTime()).equalTo(test.getTime())
  })
  it('Render', () => renderer.create(<Form />));
});
